.lds-dual-ring {
    display: inline-block;
    width: 80px;
    height: 80px;
    margin: 20% 50%;
}
.lds-dual-ring:after {
    content: " ";
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid black;
    border-color: #699f25 transparent #699f25 transparent;
    animation: lds-dual-ring 1.2s linear infinite;
}
@keyframes lds-dual-ring {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.wrap{
    display: flex;
    justify-content: center;
    margin: .5rem;
}